// Inside university component
#university {
  overflow: visible;
  li:first-of-type {
    div:first-of-type {
      li:first-of-type {
        a:first-of-type {
          &:after {
            content: ',',;
            color: #ffbf00;
          }
        }
      }
    }
  }
  >img, h1 {
    position: relative;
    left: 165px;
  }
  img {
    bottom: 8px;
  }
  h1 {
    text-transform: uppercase;
    font-size: 18px;
    bottom: 12px;
  }
  li {
    font-size: 15px;
    clear: both;
    overflow: hidden;
    border: 1px solid #c7c7c7;
    div {
      img {
        margin: 19px 8px 21px 15px;
        display: inline-block;
      }
      p {
        width: 370px;
        line-height: 18px;
        color: #666;
        display: inline-block;
        position: relative;
        bottom: 57px;
      }
      .date {
        margin-left: 148px;
        bottom: 55px;
        font-size: 12px;
        color: #b2b2b2;
      }
      div {
        position: relative;
        bottom: 73.5px;
        left: 238px;
        width: 500px;
        span {
          position: relative;
          font-size: 12px;
          color: #b2b2b2;
          margin: -3.5px;
          bottom: 0;
        }
        ul {
          display: inline;
          position: relative;
          li:first-of-type,
          li:nth-of-type(2)
          {
            a {
              &:after {
                content: ',';
              }
            }
          }
          li {
            border: none; 
            display: inline;
            a {
              font-size: 12px;
              text-decoration: none;
              color: black;
            }
          }
        }
      }
      span {
        position: relative;
        left: 148px;
        bottom: 70px;
        font-size: 12.5px;
        a {
          color: red;
          text-decoration: none;
          font-size: 12px;
          position: relative;
          bottom: 0.5px;
        }
        &.paren {
          left: 0;
        }
      }
    }
  }
}
