#news {
  overflow: visible;
  img, h1 {
    position: relative;
    bottom: 8px;
    left: 174px;
  }
  h1 {
    font-size: 18px;
    bottom: 12px;
    text-transform: uppercase;
  }
  border: none;
  ul > li {
    border: none;
    border-bottom: 1px solid #c7c7c7;
  }
  span.paren {
    color: #c7c7c7;
  }

  ul {
    li:nth-of-type(2) {
      p:nth-of-type(1) {
        padding-top: 9px;
      }
    }
    li:nth-of-type(3) {
      p:nth-of-type(1) {
        padding-top: 22px;
      }
    }
    li:nth-of-type(3) {
      border-bottom: none;
    }
    li {
      height: 70px;
      background-color: #fff;
      p {
        color: #666;
        font-size: 15px;
        margin-top: 3px;
        &:nth-of-type(2) {
          font-size: 12px;
          color: #c7c7c7;
          margin-top: 8px;
        }
      }
      span {
        font-size: 12px;
      }
      ul {
        margin: 0 -5px;
        display: inline;
        li {
          font-size: 12px;
          display: inline;
          border: none;
          &:first-of-type,
          &:nth-of-type(2)
          {
            a {
              &:after {
                content: ',';
              }
            }
          }
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
  }
  ul ul, span.paren {
    position: relative;
    left: 90px;
    bottom: 15px;
  }
}
