#community {

  overflow: visible;
  font-size: 14px;
  height: 298px;
  background: url('../quar-assets/gray_clouds_2.svg') 100% no-repeat;
  background-size: 548px 287px;
  background-position-y: 30px;
  h1 {
    text-transform: uppercase;
    font-size: 18px;
    position: relative;
  }
  &>img:first-of-type,
  h1 {
    position: relative;
    bottom: 10px;
    left: 158px;
  }
  h1 {
    bottom: 13px;
  }

  a {
    color: red;
    text-decoration: none;
  }
  li {
    &:nth-of-type(2),
    &:nth-of-type(3) {
      margin-left: 25px;
    }

    display: inline;
    position: relative;
    top: 2px;
    border: none;
    width: 60px;
    height: 22px;
    background-color: #fff;
    a {
      display: inline;
      text-decoration: none;
      font-size: 18px;
      color: #666;
    }
    .active {
      color: red;
    }
  }
  ul + div,
  ul + div + a {
    position: relative;
    top: 26px;
    color: #666;
    line-height: 18px;
    padding-left: 10px;
  }
  span {
    display: block;
    font-style: italic;
    font-size: 12px;
    margin-bottom: 23px;
  }
  img {
    position: relative;
    left: 450px;
    bottom: 12px;
  }
}
