.links {
  margin-top: (2 / 9) * 204px;
  position: relative;
  padding: 17px 0 0 17px;
  box-sizing: border-box;
  width: 100%;
  height: 204px;
  background-color: rgba(51, 51, 51, 1);
  color: #fff;
  img {
    height: 204px;
    position: absolute;
    bottom: 0px;
    left: 757px;
  }
  h1 {
    color: red;
    text-transform: uppercase;
    font-size: 12px;
    margin-bottom: 12px;
  }

  li {
    font-size: 12px;
    margin: 10px 0;
    line-height: 15px;
  }

  section {
    float: left;
    width: 195px;
  }
  section:nth-of-type(2),
  section:nth-of-type(3) {
    position: relative;
    right: 17px;
  }
}
